down(){
var touch ;
if(event.touches){
touch = event.touches[0];
}else {
touch = event;
}

var { clientX, clientY } = touch;

this.position.iX = clientX - this.$refs.timeCountBtn.offsetLeft;

this.position.iY = clientY - this.$refs.timeCountBtn.offsetTop;

this.position.mousedownState = true;
},
move(){
if(this.position.mousedownState){
var touch ;
if(event.touches){
touch = event.touches[0];
}else {
touch = event;
}


var { clientX, clientY } = touch;


var { clientHeight: pageDivY, clientWidth: pageDivX } = document.body;


var { clientHeight: actionMgrY, clientWidth: actionMgrX, style: actionMgrStyle } = this.$refs.timeCountBtn;

/* 鼠标坐标 - 鼠标与拖拽按钮的 间距坐标  得到 拖拽按钮的 左上角 x轴y轴坐标 */
var [x, y] = [clientX - this.position.iX, clientY - this.position.iY];

/* 此处判断 拖拽按钮 如果超出 屏幕宽高 或者 小于
* 设置 屏幕最大 x=全局容器x y=全局容器y 否则 设置 为 x=0 y=0
*/
if (x > pageDivX - actionMgrX) x = pageDivX - actionMgrX;
else if (x < 0) x = 0;
if (y > pageDivY - actionMgrY) y = pageDivY - actionMgrY;
else if (y < 0) y = 0;

this.betiX = ((100 * x) / pageDivX).toFixed(3);
this.betiY = ((100 * y) / pageDivY).toFixed(3);

actionMgrStyle.left = `${x}px`;
actionMgrStyle.top = `${y}px`;
actionMgrStyle.bottom = "auto";
actionMgrStyle.right = "auto";

this.$tools.setLocal('SicboiXiY',{x:this.betiX, y:this.betiY})

event.preventDefault();
}
},

end(){
this.position.mousedownState = false;
},
