setAcivation(){
this.activationStr = this.activationStr.indexOf('?bcbrefer=') > 0 ? this.activationStr.split('=')[1] : this.activationStr;
if(this.activationStr == ''){
this.showPositionValue = true;
this.toastText = '请填写邀请码';
return;
}
this.activationStr = this.activationStr.replace(/\s+/g,"");
if(this.userAddress==this.activationStr){
this.showPositionValue = true;
this.toastText = '不能绑定自己的邀请码';
return;
}
if(process.env.NODE_ENV=='development'){
if(this.activationStr.substr(0, 7) != "bcbtest"){
this.showPositionValue = true;
this.toastText = '邀请码错误';
return;
}
}else{
if(this.activationStr.substr(0, 3) != "bcb"){
this.showPositionValue = true;
this.toastText = '邀请码错误';
return;
}
}
this.$http.get(this.$req + '/check-address?addr=' + this.activationStr).then((res)=>{
if(res.data.status){
this.checkRelation();
}else{
this.showPositionValue = true;
this.toastText = '该邀请码不能绑定,请重新输入';
return;
}
})

},
checkRelation(){
this.$http.get(this.$proxy +'/checkRelation?address='+this.userAddress+'&refAddress='+this.activationStr).then((res)=>{
if(res==1){
this.createRelation();
}else{
this.showPositionValue = true;
this.toastText = '该用户不可以建立关联关系';
return;
}
})
},

createRelation(){
this.$http.get(this.$proxy +'/createRelation?address='+this.userAddress+'&refAddress='+this.activationStr+'&gameType=4').then((res)=>{
this.refAddress = this.activationStr;
