selectBet(parmas){
let betType = parmas.item;
if( Number(this.maxAddMoney[this.payUnit]) > this.betrge.max - Number(this.betBallNum)){
this.showExceedMoney = true;
this.exceedText = this.langChange?`不能超过单次下注上限 ${this.betrge.max} `:`Can't exceed the upper limit of a single bet ${this.betrge.max}`;
return;
}
if(this.canAddMoney){
betType.money[this.payUnit] += Number(this.betBallNum);
if(betType.money[this.payUnit]<=this.betRanges[this.payUnit].maxbet&&this.betBallNum < this.betRanges[this.payUnit].maxbet){

betType.click = true;
this.canAddMoney = false;
let addMoney = Number(betType.money[this.payUnit]);
if((addMoney%1).toFixed(1).split('.')[1]!='0'){
addMoney = addMoney.toFixed(1)
}else{
addMoney = addMoney.toFixed(0)
}
if(addMoney>=1000&&addMoney<10000){
betType.moneyStr[this.payUnit] = addMoney/1000+'k';
}else if(betType.money[this.payUnit]>=10000){
betType.moneyStr[this.payUnit] = addMoney/10000+'w';
}else{
betType.moneyStr[this.payUnit] = addMoney;
}
this.maxAddMoney[this.payUnit] += Number(this.betBallNum);
let Balltop = this.$refs.chips.getBoundingClientRect().top
let BallLeft = this.$refs.chips.getBoundingClientRect().left
this.$nextTick(()=>{
var top = parmas.event.currentTarget.getBoundingClientRect().top;
var left = parmas.event.currentTarget.getBoundingClientRect().left+20;
this.animatedOne(top,left,Balltop,BallLeft)
})

}else{
betType.money[this.payUnit] -= Number(this.betBallNum);
this.canAddMoney = true;
this.showExceedMoney = true;
this.exceedText = this.langChange?`不能超过单门下注上限 ${this.betRanges[this.payUnit].maxbet}`:`Can not exceed the single door betting limit of ${this.betRanges[this.payUnit].maxbet}`
console.log(betType.money[this.payUnit])
}
}
this.$store.commit("BETSHAPE", {key:parmas.item.keyName, val:parmas.item})
},
animatedOne(top,left,Balltop,BallLeft){
let that = this;
var tl = new TimelineMax({onComplete:that.animatedTwo()});
tl.to(this.$refs.chips, 0.25, {left:left, bottom:Balltop-top-10, scale:1.3, zIndex:9999,ease:Power4.easeOut});
tl.to(this.$refs.chips, 0.05, {opacity:1, scale:0.6,}, "-=0.1");
tl.add( TweenLite.to(this.$refs.chips,0.2, {opacity:0}) );
tl.add( TweenLite.to(this.$refs.chips,0,{left:'300px', scale:0.6,bottom:0,opacity:1,zIndex:-1}) );
},

animatedTwo(){
setTimeout(()=>{
this.canAddMoney = true;
},520)
},

cleanTranstion(){
this.leftImgAct = true;
},
cleanTranstionEnd(){
this.leftImgAct = false;
this.maxAddMoney[this.payUnit] = 0;
this.$store.commit('CLEARBET');
this.$store.commit("BETOBJ");
sicBohk.transactionClear('',this.payUnit).then(val =>{})
},
},
}



