import { mapState } from 'vuex'
import { setTimeout } from 'timers';
import { Promise } from 'q';
import { resolve } from 'url';
import { rejects } from 'assert';
import { error } from 'util';
export default {
    name:'audioOpen',
    data(){
        return{
            audiOpen:{
                src1:require('../assets/audio/1.mp3'),
                src2:require('../assets/audio/2.mp3'),
                src3:require('../assets/audio/3.mp3'),
                src4:require('../assets/audio/4.mp3'),
                src5:require('../assets/audio/5.mp3'),
                src6:require('../assets/audio/6.mp3'),
            }
            
        }
    },
    computed:{
        ...mapState(['lotteryinfo'])
    },
    watch:{
        'lotteryinfo.mask'(val, oldVal){
            if(val){
                this.openBobao(this.lotteryinfo.win_number)
            }
        }
    },
    methods:{
        openBobao(n){
            if(!(n instanceof Array) && n.length < 2) return
            this.audioplay(n[0]).then().then(res => {
                if(res){
                    setTimeout(() => {
                        this.audioplay(n[1]).then().then(res => {
                            setTimeout(() => {
                                this.audioplay(n[2])
                            },900)
                        })
                    },900)
                }
            })
            
        },
        audioplay(n){
            return new Promise((resolve, rejects) => {
                var _doc = document.getElementById('audiOpen_' + n)
                if(_doc == null){
                    rejects(error)
                }else{
                    _doc.muted = false;
                    _doc.load();
                    _doc.play();
                    resolve({pass:true})
                    _doc.onended = ()=> {
                        return new Promise((end_resolve, end_rejects) => {
                            end_resolve(true)
                        })
                        
                    }
                }
            })
        }
    },
    mounted(){
        
        window['aaa'] = (win)=> {
            this.openBobao(win.toString().split(''))
        }
        
        
    }
}



