
import VueQr from 'vue-qr'
import Clipboard from 'clipboard';
import BScroll from 'better-scroll';
import Spinner from 'vux/src/components/spinner/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import { mapState } from 'vuex';
export default {
name:'recomFriends',
props:{
isRecome:{type: Boolean, default: true},
},
data(){
return{
toastCtr:false,//复制成功弹框
toastText:'复制成功',
agentList:[],//推荐好友信息
isShow:false,
scroll:null,
pulldownMsg:'释放刷新',
alertHook:'none',
pullupMsg:'点击加载',
page:1,
isScroll:false,
inlineload:true,//进入加载loading
wAdress:'',//钱包地址
downAdress:"https://www.blockwallet.pro/down?bcbrefer=",
loadShow:false,

}
},
components: {
Spinner,
InlineLoading,
Loading,
Toast,
VueQr
},
watch:{
isRecome(val, oldVal){
this.wAdress = JSON.parse(window.localStorage.getItem('thisAddr')).address
if(val){
this.getRecord();
}
}
},
computed:{
...mapState(['langChange'])
},
created(){
if(this.langChange){
this.pulldownMsg = '释放刷新';
this.pullupMsg = '点击加载';
}else{
this.pulldownMsg = 'Release refresh';
this.pullupMsg = 'Click load';
}

},
mounted(){


const that = this;
this.$nextTick(()=>{
if (!this.scroll) {
this.scroll = new BScroll(this.$refs.recomBox, {
click: true,
scrollY: true,
probeType:1,






pullDownRefresh: {

threshold: 30,

stop: 50
}
})
}else{
this.scroll.refresh();
}

this.scroll.on('scroll',(pos)=>{
pos.y<0?this.isScroll = true:this.isScroll = false;
})
this.scroll.on('pullingDown', () => {
this.isShow = true;
this.page = 1;

let agenturl = this.$proxy  + '/agentList?addr='+this.wAdress+'&pageSize=15&pageIndex=0';
setTimeout(() => {
this.$http.get(agenturl).then(res => {
this.agentList = [];
this.agentList = res.data.list
this.refreshalert();
if(this.langChange){
this.pulldownMsg = '释放刷新';
this.pullupMsg = '点击加载';
}else{
this.pulldownMsg = 'Release refresh';
this.pullupMsg = 'Click load';
}

this.isShow = false;
})
this.scroll.finishPullDown()
this.scroll.refresh()
},1000)
})
})
},
methods:{
backTop(){
this.scroll.scrollTo(0, 0, 500)
},
copyQrcode(){
let that = this;
let copyBtn = new Clipboard('#copyButton');
copyBtn.on('success', e => {
that.toastCtr = true;
copyBtn.destroy()
})
copyBtn.on('error', e => {
that.toastCtr = false;
copyBtn.destroy()
})
},

refreshalert(){
this.alertHook = 'block';
setTimeout(()=>{
this.alertHook = 'none'
},1000)
},

getRecord(){

var agenturl = this.$proxy + '/agentList?addr=' + this.wAdress+'&pageSize=15&pageIndex=0';
this.$http.get(agenturl).then(res => {
if(res){
this.agentList = res.data.list;
this.inlineload = false;
if(this.agentList.length<14){
if(this.langChange){
this.pullupMsg = '没有更多了';
}else{
this.pullupMsg  = 'No more';
}
}
}
}).catch(err => {
this.inlineload = true;
})

},
ClickloadMore(){
this.loadShow = true;
if(this.langChange){
this.pullupMsg  = '加载中...';
}else{
this.pullupMsg  = 'Loading...';
}
let agenturl = this.$proxy  + '/agentList?addr='+this.wAdress+'&pageSize=15&pageIndex='+this.page;
this.$http.get(agenturl).then(res =>{
this.loadShow = false;
if(this.langChange){
this.pullupMsg = '点击加载';
}else{
this.pullupMsg  = 'Click load';
}
if(res){
if(res.data.list instanceof Array){
var _Arr = res.data.list;
this.agentList = [...this.agentList, ..._Arr];
this.page++;
if(this.agentList.length==res.data.count){
if(this.langChange){
this.pullupMsg = '没有更多了';
}else{
this.pullupMsg  = 'No more';
}

}

}
}
})
}
}
}



