
import BScroll from 'better-scroll';
import Spinner from 'vux/src/components/spinner/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import { mapState } from 'vuex';
export default {
name:'userRecord',
props:{
isUser:{type: Boolean, default: true},
},
data(){
return{
historyArr:[],
scroll:null,
isShow:false,
pulldownMsg:'释放刷新',
alertHook:'none',
pullupMsg:'点击加载',
page :2,
inlineload:true,//进入加载loading
atuoHttpest:null,
isScroll:false,
loadShow:false,
wAdress:'',//地址

}
},
computed:{
...mapState(['langChange'])
},
components: {
Spinner,
InlineLoading,
Loading,
Toast
},
watch:{
isUser(val, oldVal){
this.wAdress = JSON.parse(window.localStorage.getItem('thisAddr')).address
if(val){
this.getUserData();
}
}
},
created(){
if(this.langChange){
this.pulldownMsg = '释放刷新';
this.pullupMsg = '点击加载';
}else{
this.pulldownMsg = 'Release refresh';
this.pullupMsg = 'Click load';
}
},
methods:{
takeLottery(number){
return this.$tools.arrRecord(number);
},
backTop(){
this.scroll.scrollTo(0, 0, 500)
},
openUser(parmas){
parmas.item.click = !parmas.item.click;
this.historyArr.forEach((v,i)=>{
if(i != parmas.index){
v.click=false;
}
})
},
getUserData(){
this.historyArr = [];
let url = this.$req + '/personal-record/' + this.wAdress +'?limit=15&page=1';

this.$http.get(url).then(res => {
if(res){
var _Arr = res.data.data;
if(_Arr instanceof Array){
for(var i = 0, len =_Arr.length; i < len; i++){


_Arr[i].address=  _Arr[i].user_addr.substr(_Arr[i].user_addr.length-5),
_Arr[i].token_name = _Arr[i].token_name == 'Diamond Coin' ? 'dc' : _Arr[i].token_name,
_Arr[i].betCount = _Arr[i].amount/1000000000 + _Arr[i].token_name,
_Arr[i].betInZh = _Arr[i].amount_note.indexOf('@') > 0 ? _Arr[i].amount_note.split('@')[0] : '第一球大小单双-双',
_Arr[i].betInEh =  _Arr[i].amount_note.indexOf('@') > 0 ? _Arr[i].amount_note.split('@')[1] : 'First ball Big Small Single Double-Double',
_Arr[i].bettrx =  _Arr[i].tx_hash,
_Arr[i].openCode = _Arr[i].lottery_result,
_Arr[i].bonus = _Arr[i].bonus != 0 ? _Arr[i].bonus / 1000000000 + '' + _Arr[i].token_name : '0 '+ _Arr[i].token_name ,
_Arr[i].time = _Arr[i].tx_time.substr(5,11),
_Arr[i].round = _Arr[i].round,
_Arr[i].click = false;



}
this.historyArr = _Arr;
this.inlineload = false;
this.$nextTick(()=>{
let height = this.$refs.userBox.clientHeight;
if(this.$refs.userBoxList.clientHeight<height){
this.$refs.userBox.style.height = this.$refs.userBoxList.clientHeight-2+'px';
this.scroll.refresh();
}

})
}
}
}).catch(err => {
this.inlineload = true;
})
},

refreshalert(){
this.alertHook = 'block';
setTimeout(()=>{
this.alertHook = 'none'
},1000)
},
ClickloadMore(){
this.loadShow = true;
if(this.langChange){
this.pullupMsg  = '加载中...';
}else{
this.pullupMsg  = 'Loading...';
}
let url = this.$req + '/personal-record/' + this.wAdress +'?limit=15&page='+this.page;

this.$http.get(url).then(res =>{
this.loadShow = false;
if(this.langChange){
this.pullupMsg = '点击加载';
}else{
this.pullupMsg  = 'Click load';
}
if(res){
if(res.data.data instanceof Array){
var _Arr = res.data.data;
for(var i = 0, len =_Arr.length; i < len; i++){
_Arr[i].address = _Arr[i].user_addr.substr(_Arr[i].user_addr.length-5),
_Arr[i].token_name = _Arr[i].token_name == 'Diamond Coin' ? 'dc' : _Arr[i].token_name,
_Arr[i].betCount = _Arr[i].amount/1000000000 + _Arr[i].token_name,
_Arr[i].betInZh = _Arr[i].amount_note.indexOf('@') > 0 ? _Arr[i].amount_note.split('@')[0] : '第一球大小单双-双',
_Arr[i].betInEh =  _Arr[i].amount_note.indexOf('@') > 0 ? _Arr[i].amount_note.split('@')[1] : 'First ball Big Small Single Double-Double',
_Arr[i].bettrx =  _Arr[i].tx_hash,
_Arr[i].openCode = _Arr[i].lottery_result,
_Arr[i].bonus = _Arr[i].bonus != 0 ? _Arr[i].bonus / 1000000000 + '' + _Arr[i].token_name : '0 '+ _Arr[i].token_name  ,
_Arr[i].time = _Arr[i].tx_time.substr(5,11),
_Arr[i].round = _Arr[i].round,
_Arr[i].click = false;


}
this.historyArr = [...this.historyArr, ..._Arr];
this.page++;


if(this.historyArr.length==res.data.total){
if(this.langChange){
this.pullupMsg = '没有更多了';
}else{
this.pullupMsg  = 'No more';
}

}

}
}
})
}
},
mounted(){
this.$nextTick(()=>{
if (!this.scroll) {
this.scroll = new BScroll(this.$refs.userBox, {
click: true,
scrollY: true,
probeType:2,

pullDownRefresh: {

threshold: 30,

stop: 50
}
})
}else{
this.scroll.refresh();
}
this.scroll.on('scroll',(pos)=>{
pos.y<0?this.isScroll = true:this.isScroll = false;
})
this.scroll.on('pullingDown', () => {
this.isShow = true;
this.page = 2 
setTimeout(() => {

var rshow = true;
this.getUserData(rshow);
this.refreshalert();
if(this.langChange){
this.pulldownMsg = '释放刷新';
this.pullupMsg = '点击加载';
}else{
this.pulldownMsg = 'Release refresh';
this.pullupMsg = 'Click load';
}
this.isShow = false;
this.scroll.finishPullDown()
this.scroll.refresh()
},1000)
})
})
},
}



