export default {
    //大小单双
    high_A:function(win){
        var winArr = [];
        if(win[0] !== win[1] && win[0] !== win[2] && win[1] !== win[2]){
            var sum  = win.reduce((prev, cur) => prev + cur);
            if( sum >= 11 && sum <= 17){
                winArr.push({ betMode:'A', betMsg:'B' })//大
            }else if(sum >= 4 && sum <= 10){
                winArr.push({ betMode:'A', betMsg:'S' })//小
            }else if(sum == 5 || sum == 7 || sum == 9 || sum == 13 || sum == 15 || sum == 17){
                winArr.push({ betMode:'A', betMsg:'O' })//单
            }else if(sum == 4 || sum == 6 || sum == 8 || sum == 10 || sum == 14 || sum == 16){
                winArr.push({ betMode:'A', betMsg:'E' })//单
            }
        }
        return winArr
    },
    //围骰
    high_B:function(win){
        var winArr = [];
        if(win[0] == win[1] && win[1] == win[2]){
            var sum = win[0];
            if(sum == 1) winArr.push({ betMode:'B', betMsg:'1' },{ betMode:'B', betMsg:'0' })
            else if(sum == 2) winArr.push({ betMode:'B', betMsg:'2' },{ betMode:'B', betMsg:'0' })
            else if(sum == 3) winArr.push({ betMode:'B', betMsg:'3' },{ betMode:'B', betMsg:'0' })
            else if(sum == 4) winArr.push({ betMode:'B', betMsg:'4' },{ betMode:'B', betMsg:'0' })
            else if(sum == 5) winArr.push({ betMode:'B', betMsg:'5' },{ betMode:'B', betMsg:'0' })
            else if(sum == 6) winArr.push({ betMode:'B', betMsg:'6' },{ betMode:'B', betMsg:'0' })
        }
        return winArr
    },
    //对子
    high_C:function(win){
        var winArr = [];
        var mapsum = win.reduce((prev, cur) => prev.set(cur, (prev.get(cur) || 0) + 1), new Map())
        if(mapsum.get(win[0]) > 1) winArr.push({ betMode:'C', betMsg:win[0] })
        else if(mapsum.get(win[1]) > 1) winArr.push({ betMode:'C', betMsg:win[1] })
        else if(mapsum.get(win[2]) > 1) winArr.push({ betMode:'C', betMsg:win[2] })
        return winArr
    },
    //点数
    high_D:function(win){
        var winArr = [];
        var sum = win.reduce((prev, cur) => prev + cur)
        if(sum >= 4 && sum <= 17){
            winArr.push({ betMode:'D', betMsg:sum })
        }
        return winArr;
    },
    //牌九
    high_E:function(win){
        if(win[0] == win[1] && win[1] == win[2]) return
        var winArr = [];
        var win_sort = [...win].sort((x, y) => x - y);
        if(win_sort[0] == win_sort[1]) winArr.push({ betMode:'E', betMsg:win_sort[1] + ',' + win_sort[2] })
        else if (win_sort[1] == win_sort[2]) winArr.push({ betMode:'E', betMsg:win_sort[0] + ',' + win_sort[1] })
        else winArr.push({ betMode:'E', betMsg:win_sort[0] + ',' + win_sort[1] }, { betMode:'E', betMsg:win_sort[0] + ',' + win_sort[2] },{ betMode:'E', betMsg:win_sort[1] + ',' + win_sort[2] })
        return winArr;
    },
    //单点
    high_F:function(win){
        var winArr = [];
        if(win.indexOf('1') > -1 || win.indexOf(1) > -1) winArr.push({ betMode:'F', betMsg:'1' })
        else if(win.indexOf('2') > -1 || win.indexOf(2) > -1) winArr.push({ betMode:'F', betMsg:'2' })
        else if(win.indexOf('3') > -1 || win.indexOf(3) > -1) winArr.push({ betMode:'F', betMsg:'3' })
        else if(win.indexOf('4') > -1 || win.indexOf(4) > -1) winArr.push({ betMode:'F', betMsg:'4' })
        else if(win.indexOf('5') > -1 || win.indexOf(5) > -1) winArr.push({ betMode:'F', betMsg:'5' })
        else if(win.indexOf('6') > -1 || win.indexOf(6) > -1) winArr.push({ betMode:'F', betMsg:'6' })
        return winArr;
    },

    init:function(win){
        if(win instanceof Array && win.length == 3){
            var that = this, highArr = [];
            highArr = [
                ...that.high_A(win),
                ...that.high_B(win),
                ...that.high_C(win),
                ...that.high_D(win),
                ...that.high_E(win),
                ...that.high_F(win)
            ];
            return highArr
        }else{
            return null
        }
    }
}



