import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store/index.js'
import tools from './plugins/tools'
import './assets/css/reset.css'
import './assets/css/main.css'
import vueTap from 'v-tap'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'

import VueI18n from 'vue-i18n'
import axios from './http/http.js'
import './plugins/bridge.js'
import './plugins/vConsole.js'
import VueTouch from './plugins/e.js';
import req from './plugins/req.js';
import './plugins/circles.min.js'
import HighBetMsg from './plugins/HighBetMsg.js'

Vue.config.productionTip = false

Vue.prototype.$tools = tools;
Vue.prototype.$high = HighBetMsg;
Vue.prototype.$http = axios;
Vue.prototype.$req = req.reqUrl;
Vue.prototype.$soc = req.reqSoc;
Vue.prototype.$proxy = req.reqproxy;

Vue.use(vueTap)
Vue.use(VueI18n)

if(window.localStorage.getItem('lang')){
store.commit('LANGCHANGE',window.localStorage.getItem('lang')=='zh-CN'?true:false)
}else{
window.localStorage.setItem('lang','zh-CN')
store.commit('LANGCHANGE',true)
}

const i18n = new VueI18n({
locale: 'zh', // 语言标识
messages: {
'zh': require('./assets/lang/zh'),
'en': require('./assets/lang/en')
}
})

Vue.directive('transfer-dom', TransferDom)
Vue.component('popup', Popup)


Vue.directive("longtap", {
bind: function (el, binding) {
new VueTouch(el, binding, "longtap");
}
});



new Vue({
el: '#app',
router,
i18n,
store,
components: { App },
template: '<App/>',
mounted() {
},
methods:{
}
})



