import store from '../store/index.js'


//发送短链接获取钱包用户信息
window.final = function () { //一进入discovery页面马上请求获取钱包地址
    let url = `bcbwallet://req_info=bcbinforeq&listWallets`;
    // document.location = url;
    // window.webkit.messageHandlers.BCBPay.postMessage(url);
    let device = window.device();
    if (device == 'Android' || device == 'WindowsPhone') {
      var iframe = document.createElement('iframe')
      iframe.style.width = '1px'
      iframe.style.height = '1px'
      iframe.style.display = 'none'
      iframe.src = url
      document.body.appendChild(iframe)
      setTimeout(function () {
            iframe.remove()
      }, 100)
    }
    if (device == 'iPhone') {
        window.webkit && window.webkit.messageHandlers.BCBPay.postMessage(url);
    }
};
  

//发送短链接
window.JSBridge = function (urlData) {
    let device = window.device();
    if (device == 'iPhone') {
        window.webkit && window.webkit.messageHandlers.BCBPay.postMessage("bcbwallet://req_pay="+urlData);
    }
    if (device == 'Android') {
        // var url = "bcbwallet://" + urlData
        var url = "bcbwallet://req_pay=" + urlData
        var iframe = document.createElement('iframe')
        iframe.style.width = '1px'
        iframe.style.height = '1px'
        iframe.style.display = 'none'
        iframe.src = url
        document.body.appendChild(iframe)
        setTimeout(function () {
            iframe.remove()
        }, 100)
    }
    
}
  
//app调用返回的钱包用户信息
window.listWallets = function (wallets) { //返回玩家注册名，钱包地址
    // window.console.log(wallets, typeof wallets[0], 'app message wallets');
    if (wallets) {
        if (wallets.length === 1) {
            localStorage.setItem('thisAddr', wallets[0]); // 返回的直接是string 
        }
        sessionStorage.setItem('playerAddr', JSON.stringify(wallets)); // 所有钱包列表
    }
};
  
//支付成功后app调用我们的方法
window.inform = function (data) {
    // window.console.log(data, 'pay app cb');
    // let status = sessionStorage.getItem('status')
    if (data) {
        //支付成功
        var payUnit = window.sessionStorage.getItem('payUnit');
        sicBohk.recordOldMoney('', payUnit).then(res => {
            if(res.pass){
                store.commit("CLEARBET");
            }
        });
        window.flag = false;
        store.commit('BETSIGN',{ sign:true, zhifu:'success' })
    } else {
        //支付失败
        //store.commit('BETSIGN',{ sign:true, zhifu:'warn' })
    }
  
}
  //判断设备
window.device = function () {
    var u = window.navigator.userAgent;
    var device = '';
    if (u.indexOf('Android') > -1 || u.indexOf('Linux') > -1) {
        //安卓手机
        device = 'Android';
    } else if (u.indexOf('iPhone') > -1) {
        //苹果手机
        device = 'iPhone'
    } else if (u.indexOf('Windows Phone') > -1) {
        //winphone手机
        device = 'WindowsPhone'
    }
    return device;
}
  
