
import BScroll from 'better-scroll';
import Spinner from 'vux/src/components/spinner/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import { mapState } from 'vuex';
export default {
name:'lotterData',
props:{
isHide:{type: Boolean, default: true},
},
data(){
return{
historyList:[],//历史记录
scroll:null,
pulldownMsg:'释放刷新',
pullupMsg:'点击加载',
isShow:false,
inlineload:true,//进入加载loading
page:2,
alertSuccsee:'none',//刷新成功
loadShow:false,//点击加载
isScroll:false,//回到顶部

dongTaiHeight:270,//弹框动态高度
}
},
watch:{
isHide(val, oldVal){
if(val){
this.getUserHistory();
}
}
},
computed:{
...mapState(['langChange'])
},
created(){
if(this.langChange){
this.pulldownMsg = '释放刷新';
this.pullupMsg = '点击加载';
}else{
this.pulldownMsg = 'Release refresh';
this.pullupMsg = 'Click load';
}
},
components: {
Loading,
Spinner,
InlineLoading
},
methods:{
takeLottery(number){
return this.$tools.arrRecord(number);
},
openMation(parmas){
parmas.item.show = !parmas.item.show;
this.historyList.forEach((m,n)=>{
if(n!==parmas.index){
m.records.forEach((j,k)=>{
j.show=false
})
}else{
m.records.forEach((j,k)=>{
if(k!=parmas.index2){
j.show=false
}
})
}
});
},

refreshalert(){
this.alertSuccsee = 'block';
setTimeout(()=>{
this.alertSuccsee = 'none'
},1000)
},

backTop(){
this.scroll.scrollTo(0, 0, 500)
},
getUserHistory(){
var url = this.$req  + '/record?limit=15&page=1';
this.$http.get(url).then(res => {
if(res){
var _Arr = res.data.data;
if(_Arr instanceof Array){
for(var j = 0, jen = _Arr.length; j < jen; j++){
for(var k = 0, ken = _Arr[j].records.length; k < ken; k++){
_Arr[j].records[k].show = false
_Arr[j].records[k].amount = _Arr[j].records[k].amount/1000000000
_Arr[j].records[k].bonus = _Arr[j].records[k].bonus/1000000000
_Arr[j].records[k].user_addr_long = _Arr[j].records[k].user_addr
_Arr[j].records[k].user_addr = _Arr[j].records[k].user_addr.substr(_Arr[j].records[k].user_addr.length-5)
_Arr[j].records[k].tx_time = _Arr[j].records[k].tx_time.substr(5,11)
_Arr[j].records[k].token_name = _Arr[j].records[k].token_name == 'Diamond Coin' ? 'dc' : _Arr[j].records[k].token_name
if(_Arr[j].records[k].amount_note.indexOf('@')>0){
_Arr[j].records[k].betInZh = _Arr[j].records[k].amount_note.split('@')[0]
_Arr[j].records[k].betInEn = _Arr[j].records[k].amount_note.split('@')[1]
}else{
_Arr[j].records[k].betInZh = '第一球大小单双-双';
_Arr[j].records[k].betInEn = 'First ball Big Small Single Double-Double';
}
}
var win_number = [];
for (var codePoint of  _Arr[j].win_number) {
win_number.push(codePoint)
}
}
this.historyList = _Arr;
this.inlineload = false;
this.$nextTick(()=>{
let height = this.$refs.allBox.clientHeight;
if(this.$refs.allBoxData.clientHeight<height){
this.$refs.allBox.style.height = this.$refs.allBoxData.clientHeight-2+'px';
this.scroll.refresh();
}

})
}
}
}).catch(err => {
this.inlineload = true;
})
},

getPeriods(){
var tanHeight = this.$refs.allBox.clientHeight;
this.dongTaiHeight = tanHeight - 30;
if(this.historyList.length>0){
this.$nextTick(()=>{
if(this.dongTaiHeight>this.$refs.allBoxData.clientHeight){
this.$refs.allBoxData.style.height = this.dongTaiHeight + 10 +'px';
}else{
this.$refs.allBoxData.style.height = this.$refs.allBoxData.clientHeight;
}
this.scroll.refresh();
})
}

},

ClickloadMore(){
this.loadShow = true;
if(this.langChange){
this.pullupMsg  = '加载中...';
}else{
this.pullupMsg  = 'Loading...';
}
let url = this.$req  + '/record?limit=15&page='+this.page;
this.$http.get(url).then(res =>{
this.loadShow = false;
if(this.langChange){
this.pullupMsg = '点击加载';
}else{
this.pullupMsg  = 'Click load';
}
if(res){
if(res.data.data instanceof Array){
var _Arr = res.data.data;
for(var j = 0, len =_Arr.length; j < len; j++){
for(var k = 0, ken = _Arr[j].records.length; k < ken; k++){
_Arr[j].records[k].show = false
_Arr[j].records[k].amount = _Arr[j].records[k].amount/1000000000
_Arr[j].records[k].bonus = _Arr[j].records[k].bonus/1000000000
_Arr[j].records[k].user_addr_long = _Arr[j].records[k].user_addr
_Arr[j].records[k].user_addr = _Arr[j].records[k].user_addr.substr(_Arr[j].records[k].user_addr.length-5)
_Arr[j].records[k].tx_time = _Arr[j].records[k].tx_time.substr(5,11)
_Arr[j].records[k].token_name = _Arr[j].records[k].token_name == 'Diamond Coin' ? 'dc' : _Arr[j].records[k].token_name
if(_Arr[j].records[k].amount_note.indexOf('@')>0){
_Arr[j].records[k].betInZh = _Arr[j].records[k].amount_note.split('@')[0]
_Arr[j].records[k].betInEn = _Arr[j].records[k].amount_note.split('@')[1]
}else{
_Arr[j].records[k].betInZh = '第一球大小单双-双';
_Arr[j].records[k].betInEn = 'First ball Big Small Single Double-Double';
}
}
}
this.historyList = [...this.historyList, ..._Arr];
this.page++;


if(this.historyList.length==res.data.total){
if(this.langChange){
this.pullupMsg = '没有更多了';
}else{
this.pullupMsg  = 'No more';
}

}

}
}
})
}
},
mounted(){
this.$nextTick(()=>{
if (!this.scroll) {
this.scroll = new BScroll(this.$refs.allBox, {
click: true,
scrollY: true,
probeType:2,

pullDownRefresh: {

threshold: 30,

stop: 20
}
})
}else{
this.scroll.refresh();
}
this.scroll.on('scroll',(pos)=>{
pos.y<-17?this.isScroll = true:this.isScroll = false;
})
this.scroll.on('pullingDown', () => {
this.isShow = true;
this.page = 2;
setTimeout(() => {

var rshow = true;
this.getUserHistory(rshow);
this.refreshalert();
if(this.langChange){
this.pulldownMsg = '释放刷新';
this.pullupMsg = '点击加载';
}else{
this.pulldownMsg = 'Release refresh';
this.pullupMsg = 'Click load';
}
this.isShow = false;
this.scroll.finishPullDown()
this.scroll.refresh()
},1000)
})
})
}
}



