
import InlineLoading from 'vux/src/components/inline-loading/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import { setTimeout } from 'timers';
import { mapState } from 'vuex';
export default {
name:'agencyincome',
data(){
return{
proxyData:[],//代理收益列表
recordList: [],//返佣记录列表
levelList:[],//我的下级列表
myTeamCountDto:{},//我的下级人数
searchData:'',//input搜索框
toastText:'',//搜索提示
isShowSearch:false,//提示内容弹出
searchAdress:'搜索地址',
isSearchLoding:false,//搜索loding
levelAdress:'',//下级地址
moreDetail:{
mySubDetailDto:{
betMap:{},
rebateMap:{},
getMap:{},
},
personalList:[],
teamCountDto:{},
teamDetail:{
betMap:{},
rebateMap:{},
getMap:{},
},
teamList:[],
},//更多详情
moreShow:false,//更多显示隐藏
isPersonal:false,//个人显示隐藏
isTeam:false,//团队显示隐藏
levelLoding:true,//我的下级列表Loding
recordLoing:true,//返佣记录Loding
amountLoing:true,//今日业绩额 || 我的返佣率loading
amount:0,//今日业绩额
rate:0,//我的返佣率
proxylayerData:[//返佣记录-我的下级
{ name:'返佣记录', act: true },
{ name:'我的下级', act: false },
],
proxylayerEnd:false,//返佣记录-我的下级
pageNum: 0,
isPop:false,//返佣设置弹框
setNum:'',//修订返佣
wAdress:'bcbtestC1CUeNd3bwVqXoxCAwhztwXBBrJwEakzN'
}
},
props:{
isAgency:{type: Boolean, default(){ return false } },
},
components:{
Toast,
InlineLoading
},
computed:{
...mapState(['langChange'])
},
watch:{
isAgency(val, oldVal){
if(val){
this.agencymyProxy();
this.agencygetCommissionRate();
this.getrebateRecord();
}
}
},
methods:{

agencygetCommissionRate(){
this.amountLoing = true;
this.$http.get(this.$proxy + `/getCommissionRate?address=${this.wAdress}`).then(res => {
if(res.flag){
this.rate = res.data.rate;
this.amount = res.data.amountStr;
}
this.amountLoing = false
})
},

agencymyProxy(){
this.$http.get(this.$proxy + `/myProxy?address=${this.wAdress}&gameType=1`).then(res => {
if(res.flag){
var result = JSON.parse(res.data)
var totalRebateList = result.totalRebateList,
settleList = result.settleList,
unsettleList = result.unsettleList;
var myProxyList = [];
var bcbparam = {}, usdxparam = {}, dcparam = {}, xtparam = {};
for(var i = 0; i < 4; i++){
totalRebateList[i].coinType == 'BCB' ? (bcbparam.totalRebate = totalRebateList[i].amountStr, bcbparam.coinType = 'BCB'):
totalRebateList[i].coinType == 'USDX' ? (usdxparam.totalRebate = totalRebateList[i].amountStr, usdxparam.coinType = 'USDX'):
totalRebateList[i].coinType == 'DC' ? (dcparam.totalRebate = totalRebateList[i].amountStr, dcparam.coinType = 'DC'):
totalRebateList[i].coinType == 'XT' ? (xtparam.totalRebate = totalRebateList[i].amountStr, xtparam.coinType = 'XT'):'';
settleList[i].coinType == 'BCB' ? bcbparam.settle = settleList[i].amountStr:
settleList[i].coinType == 'USDX' ? usdxparam.settle = settleList[i].amountStr:
settleList[i].coinType == 'DC' ? dcparam.settle = settleList[i].amountStr:
settleList[i].coinType == 'XT' ? xtparam.settle = settleList[i].amountStr:'';
unsettleList[i].coinType == 'BCB' ? bcbparam.unsettle = unsettleList[i].amountStr:
unsettleList[i].coinType == 'USDX' ? usdxparam.unsettle = unsettleList[i].amountStr:
unsettleList[i].coinType == 'DC' ? dcparam.unsettle = unsettleList[i].amountStr:
unsettleList[i].coinType == 'XT' ? xtparam.unsettle = unsettleList[i].amountStr:'';
}
myProxyList.push(bcbparam, usdxparam, dcparam, xtparam)
this.proxyData = myProxyList;
}
})
},

changeProxylayer(param){
param.index == 1?this.getLevel():this.getrebateRecord();
for(var i = 0, len = this.proxylayerData.length; i < len; i++){
if(i == param.index){
this.proxylayerData[i].act = true;
this.proxylayerEnd = this.proxylayerData[i].name == '我的下级' ? true : false;
}else{
this.proxylayerData[i].act = false;
}
}
},

getrebateRecord(){
this.recordLoing = true;
this.$http.get(this.$proxy + `/rebateRecord?address=${this.wAdress}&gameType=1&pageSize=10&pageNum=${this.pageNum}`).then(res =>{
if(res.flag){
this.recordLoing = false;
this.recordList = this.recordList.concat(res.data)
}

})
},


getLevel(){
this.levelLoding = true;
this.$http.get(this.$proxy + `/getMydirectSub?address=${this.wAdress}`).then(res =>{
if(res.flag){
this.levelList = res.data.list;
this.myTeamCountDto = res.data.myTeamCountDto;
}
this.levelLoding = false;
})
},

clickMore(param){
this.moreShow = true;
console.log(param)
this.$http.get(this.$proxy + `/getDetail?address=${param.item.address}`).then(res =>{
if(res.flag){
this.moreDetail = res.data;
}

})
},

analysisNum(num){
num == '1'?num ='骰子':num=='2'?num ='龙虎斗':num=='3'?num ='百家乐':num ='时时彩';
return num;
},

OpenPersonal(){
this.isPersonal =!this.isPersonal;
},

OpenTeam(){
this.isTeam =!this.isTeam;
},

searchClick(){
if(this.searchData == ""){
this.isShowSearch = true;
this.toastText = "请输入地址";
return;
}
this.searchAdress = '搜索中...'
this.isSearchLoding = true;
var searchUrl = this.$proxy + `/search?address=bcbtestALNVrzjwr2YqF4n35qLR5sGaWqe3T5zXJ&parent=bcbtest7PGNHsqatyMncUSRaiA6ZgPruMMipX4F5`;

this.$http.get(searchUrl).then(res =>{
if(res.flag){
this.moreDetail = res.data;
this.moreShow = true;
}else{
this.isShowSearch = true;
this.toastText = res.message;
}
this.searchAdress = '搜索地址';
this.isSearchLoding = false;

})
},

clickSetting(){
var setUrl = this.$proxy + `/getSettingPermission?address=bcbtestALNVrzjwr2YqF4n35qLR5sGaWqe3T5zXJ`;

this.$http.get(setUrl).then(res =>{
if(res.flag&&!res.data.setPermission){
this.isPop = true;
}else{
this.isShowSearch = true;
this.toastText = "您没有设置权限";
}
})
},
closePop(){
this.isPop = false;
},

saveSet(){
let saveData={
"address":"bcbtest7QeuE8nWsK2MSnuS6E6gsD987AKVGdDcN",
"parent":"bcbtestEuKuP5DtuYawJSVDYgLqCDwsKbHPqTw63",
"rate":"0.0069"
}
this.$http.post(this.$proxy + `/setSettingRate`,
JSON.stringify(saveData),
{headers:{'Content-Type':'application/json;charset=UTF-8'}}
).then(res =>{
if(res.flag){
console.log(res)
}else{

}
})
}

}
}



