
import InlineLoading from 'vux/src/components/inline-loading/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import { mapState } from 'vuex';
export default {
name:'lotterHistory',
props:{
isShow:{type: Boolean, default: true},
},
data(){
return{
siboList:[],
inlineload:true,//加载loading...

}
},
computed:{
...mapState(['langChange'])
},
components: {
InlineLoading,
Loading,
Toast
},
watch:{
isShow(val, oldVal){
if(val){
this.getLottery();
}
}
},
methods:{
takeLottery(number){
return this.$tools.arrRecord(number);
},
getLottery(){
this.$http.get(this.$req+`/result-list`).then(res => {
for(var i=0;i<res.data.length;i++){
res.data[i].rounds = res.data[i].round.toString().slice(6);
}
this.siboList = res.data;
this.inlineload = false;
}).catch(err => {
this.inlineload = true;
})

},
},
}



