
import Toast from 'vux/src/components/toast/index.vue'

import { setTimeout } from 'timers';
import audioOpen from './components/audioOpen.vue'
export default {
name: 'App',
data(){
return{
bgmSong:require('./assets/audio/bgm4.mp3'),//音乐
beginBet:require('./assets/audio/beginBet.mp3'),//开始音乐
stopBet:require('./assets/audio/stopBet.mp3'),//停止投注音乐
successBet:require('./assets/audio/successBet.mp3'),//投注成功音乐
}
},
components:{
Toast,
audioOpen
},
created(){
},
mounted(){
document.querySelector('#audio1').muted = true;
document.querySelector('#audio2').muted = true;
document.querySelector('#audio3').muted = true;
document.querySelector('#audio4').muted = true;
document.getElementById('app').style.display = 'block';
document.getElementById('appLoading').style.display = 'none';
},
beforeDestroy(){
window.enterPortscreen();
window.enterNormalscreen();
},
methods:{
}
}



