import * as type from './type.js';
import tools from '../plugins/tools.js';

export default {
    [type.ISLOAD]:(state,val) => {
        state.isLoad = val;
    },
    [type.LANGCHANGE]:(state,val) => {
        state.langChange = val
        window.sessionStorage.setItem('langChange',val);
    },
    [type.PAYUNIT]:(state,type) => {
        type = tools.isEmpty(type) ? 'bcbt' : type == 'Diamond Coin' ? 'dc' : type;
        window.sessionStorage.setItem('payUnit',type);
        state.payUnit = type;
        state.betrge = state.betRanges[type];//设置币种投注注额范围
    },
    [type.SHOWWALLET]:(state,showWaller) => { 
        state.showWaller = showWaller;
    },
    [type.WALLETADRESS]:(state,walletAdress) => { 
        state.walletAdress = walletAdress;
        sessionStorage.setItem('walletAddress', walletAdress.address);
        localStorage.setItem('thisAddr', JSON.stringify(walletAdress));
    },
    /**
     * 各大币种筹码
     */
    [type.BETMONEYARR]:(state, bet) => {
        tools.isEmpty(bet) ? bet = 'bcbt' : bet;
        window.sessionStorage.setItem('payUnit',bet);
        state.betMoneyArr = state.betMoneyArrs[bet];
        //匹配切换币种状态
        for(var i = 0, len = state.betList.length; i < len; i++){
            state.betList[i].value == bet ? state.betList[i].act = true : state.betList[i].act = false;
        }
        state.payUnit = bet;
        //设置
        for(var j = 0, jen = state.betMoneyArr.length; j < jen; j++){
            if(state.betMoneyArr[j].click){
                state.betMoney = state.betMoneyArr[j].num;
                state.moneyIcon = state.betMoneyArr[j];
                state.betBallNum = state.betMoneyArr[j].num
            }
        }
    },
    /**
     * 选中的筹码
     */
    [type.MONEYICON]:(state, moneyIcon) => {
        state.moneyIcon = moneyIcon
    },
    /**
     * @param 投注金额
     */
    [type.BETBALLNUM]:(state,val)=>{
        state.betBallNum = val;
    },
    //投注集合
    [type.BETSHAPE]:(state, betShape) => {
        state.betShape[betShape.key] = betShape.val;//初始化赋值
        let $payUnit = window.sessionStorage.getItem('payUnit');
        let _BetShape = state.betShape;
        let _BetObj = state.betObj;
        for(var v in _BetShape){
            if(!_BetObj[v]&&_BetShape[v].money[$payUnit]!=0){
                state.betObj[v] = _BetShape[v]
            }
        }
    },
    //切换币种重新计算所在币种拖住集合
    [type.RESETBETSHAPE]:(state) => {
        state.betObj = {};
        let $payUnit = window.sessionStorage.getItem('payUnit');
        let _BetShape = state.betShape;
        let _BetObj = state.betObj;
        for(var v in _BetShape){
            if(!_BetObj[v]&&_BetShape[v].money[$payUnit]!=0){
                state.betObj[v] = _BetShape[v]
            }
        }
        console.log(state.betObj)
    },
    //所在币种投注集合
    [type.BETOBJ]:(state) => {
        let $payUnit = window.sessionStorage.getItem('payUnit');
        let _BetObj = state.betObj;
        for(var v in _BetObj){
            if(_BetObj[v].money[$payUnit]==0){
                delete state.betObj[v]
            }
        }
        console.log(state.betObj)
    },
    //重置投注合集 betShape
    [type.CLEARBET]:(state) =>{
        let $payUnit = window.sessionStorage.getItem('payUnit');
        for(var v in state.betShape){
            state.betShape[v].money[$payUnit] = 0;
        }
    },
    /** 
     * 投注支付状态 
     */
    [type.BETSIGN]:(state, betSign) => {
        state.betSign = betSign;
    },
    /**
     * 其他玩家投注
     */
    [type.OTHERBET]:(state,msg) => {
        state.otherBet = !state.otherBet;
        state.othertBetData = msg
    },
    /**
     * 当前游戏信息
     */
    [type.LOTTERYINFO]:(state,lotteryinfo) => {
        // left_second
        if(lotteryinfo.left_time !== null){
            var left_time = lotteryinfo.left_time.toString();
            lotteryinfo.A = left_time.length > 1 ? Number(left_time.split('')[0]) : '0';
            lotteryinfo.B = left_time.length > 1 ? Number(left_time.split('')[1]) : left_time;
            state.lotteryinfo = lotteryinfo;
            state.lotteryinfo.win_number = null;//正在执行的一轮，开奖为null
            state.lotteryinfo.mask = false;//正在执行的一轮，开奖状态为null
            state.lotteryinfo.isnew = false;//正在执行的一轮. 新的一轮状态为false
            state.personalSettleNotice.personal = false;//关闭开奖
            window.myCircle.update(left_time,250)
        }
        
    },
    /**
     * 更新游戏信息，开奖 期号
     */
    [type.UPDATELOTTERYINFO]:(state, lotteryinfo) => {
        state.lotteryinfo = lotteryinfo;
        state.lotteryinfo.left_time = 0;
        state.lotteryinfo.mask = true;
      
    },
    /**
     * 上一期游戏信息
     */
    [type.LATESTLOTTERYINFO]:(state, latestlotteryinfo) => {
        state.latestlotteryinfo = latestlotteryinfo;
        state.lotteryinfo.isnew = true;//开始新的一轮
    },
    /**
     * 背景音乐开关
     */
    [type.BGSOUND]:(state,val) => {
        state.bgSound = val;
    },
    [type.OTHERSOUND]:(state,val) => {
        state.otherSound = val; 
    },
    /**
     * 个人结算通知
     */
    [type.PERSONALSETTLENOTICE]:(state, personalSettleNotice) => {
        var bizhong = state.payUnit == 'bcbt' ? 'BCB' : 
            state.payUnit == 'xt' ? 'XT':
            state.payUnit == 'dc' ? 'DC':
            state.payUnit == 'usdx' ? 'USDX':'';
        
        personalSettleNotice.value.forEach((v, i) => {
            if(bizhong == v.tokenName){
                personalSettleNotice.winAmount = ((v.winAmount/1000000000).toFixed(2)).split('');
            }
        });
        personalSettleNotice.personal = true;
        state.personalSettleNotice = personalSettleNotice
        
    }
}


