import store from '../store/index.js';
import req from '../plugins/req.js';
const _SOCKET = {
    lockReconnect:false,//避免重复连接

    ws:null,//WebSocket的引用

    wsUrl:req.reqSoc,//后端socket链接

    reconnectTime:null,
    
    lockDestory:true,//退出路由~~~！！！锁死它！！！！

    flag:{ round:0, win_number:null, type:'' },

    userAddress:'',//用户地址

    createWebSocket:function(url){
        var that = this;
        try {
            let waStr = '';
            process.env.NODE_ENV=='development'?waStr='ws://':waStr='wss://'
            that.ws = new WebSocket(waStr + url);
            that.initEventHandle();
        } catch (e) {
            that.reconnect(that.wsUrl);
        }
    },

    reconnect:function(url){
        var that = this;
        if(that.lockReconnect){
            return;
        }else if(that.lockDestory){
            that.lockReconnect = true;
            //没连接上会一直重连，设置延迟避免请求过多
            that.reconnectTime = setTimeout(function () {
                that.createWebSocket(that.wsUrl);
                console.log("正在重连，当前时间"+new Date())
                that.lockReconnect = false;
            }, 5000); //这里设置重连间隔(ms)
        }else{
            return;
        }
    },

    initEventHandle:function(){
        //初始化开始
        var that = this;
        // 连接成功建立后响应
        that.ws.onopen = function() {
            // console.log("成功连接");
            var _user = { type:"userAddressSubscribe",  address:that.userAddress}
            that.ws.send('{"type":"roundInfo"}')
            console.log(_user)
            that.ws.send(JSON.stringify(_user))
            //心跳检测重置
            that.heartCheck().reset().start();
        }

        // 收到服务器消息后响应
        that.ws.onmessage = function(e) {
            var msg = eval('(' + e.data + ')');
            if(msg.type == "pong"){
                /**
                 * @param 心跳
                 */
            }else if(msg.type =='personalSettleNotice'){
                /**
                 * @param 个人结算通知
                 */
                console.log(msg.data)
                store.commit('PERSONALSETTLENOTICE', msg.data)//提交个人结算通知

            }else if(msg.type == 'LatestWinNum'){
                /**
                 * @param 上期通知
                 */
                store.commit('LATESTLOTTERYINFO', {...msg.data, type:'LatestWinNum'})//提交上一期的开盘和期号

            }else if(msg.type == "globalSettleNotice"){
                /**
                 * @param 全局结算通知
                 */
                store.commit('UPDATELOTTERYINFO', msg.data)//提交游戏开奖信息和期号
                that.flag.win_number = msg.data.win_number;//保存这一期的开奖
                that.flag.round = msg.data.round;//保存这一期的期号
                that.flag.type = 'globalSettleNotice'
                // setTimeout(()=>{
                    
                // },2000)
                // console.log(msg)
            }else if(msg.type == "userAddressSubscribe"){
                /**
                 * @param 用户地址订阅
                 */

                console.log("用户地址订阅", msg)
                // console.log(msg)
            }else if(msg.type == "newRound"){
                /**
                 * @param 新的一轮通知
                 */
                store.state.isNew = !store.state.isNew;
                if(that.flag.type == 'globalSettleNotice'){
                    var obj = { ...that.flag }
                    store.commit('LATESTLOTTERYINFO',obj)//提交上一期的开盘和期号
                }
                sicBohk.dielight().then(res => {
                    sicBohk.transactionAll().then(val => {})
                })
                //store.commit('LOTTERYINFO',msg.data)//提交游戏倒计时信息
            }else if(msg.type == "RoundInfo"){
                /**
                 * @param 游戏倒计时通知
                 */
                store.commit('LOTTERYINFO',msg.data)//提交游戏倒计时信息
            }else if(msg.type == "placeNotice"){
                store.commit('OTHERBET',msg);
            }
        }

        // 连接关闭后响应
        that.ws.onclose = function() {
            console.log("关闭连接");
            that.lockDestory = true;
            that.reconnect(that.wsUrl);//重连
        }
        that.ws.onerror = function () {
            console.log("重连");
            that.lockDestory = true;
            that.reconnect(that.wsUrl);//重连
        }

    },
    heartCheck:function(){
        var that = this;
        //心跳检测
        return {
            timeout: 30000,//毫秒
            timeoutObj: null,
            serverTimeoutObj: null,
            reset: function(){
                var self = this;
                self.timeoutObj = null;
                self.serverTimeoutObj = null;
                clearTimeout(self.timeoutObj);
                clearTimeout(self.serverTimeoutObj);
                return self;
            },
            start: function(){
                var self = this;
                self.timeoutObj = setTimeout(function(){
                    //这里发送一个心跳，后端收到后，返回一个心跳消息
                    that.ws.send('{"type":"ping"}');
                }, self.timeout)        
            }
        }
    },
    //关闭socket
    closeSocket:function(){
        this.heartCheck().reset();
        this.lockDestory = false;
        this.ws && this.ws.close();
    },
    init:function(userAddress){
        this.userAddress = userAddress || 'bcbtest2MHEqnm8QyUfHJPYKfGmNv2ccXeexfxt8',
        console.log("钱包地址：", userAddress)
        this.createWebSocket(this.wsUrl);
        var myCircle = Circles.create({
            id:                  'timeCountX',
            radius:              24,
            value:               30,
            maxValue:            100,
            width:               4,
            duration:            400,
            wrpClass:            'circles-wrp',
            textClass:           'circles-text',
            valueStrokeClass:    'circles-valueStroke',
            maxValueStrokeClass: 'circles-maxValueStroke',
            styleWrapper:        true,
            styleText:           true
        });
        window.myCircle = myCircle;
    }
}
export default _SOCKET;


