import Vue from 'vue'
import Router from 'vue-router'
import Sicbo from '../views/sicbo'

Vue.use(Router)

const router = new Router({
    mode: 'history',  
    routes: [
        {
            path: '/',
            redirect: { name: 'begin' },
        },
        {
            path: '/begin',
            name: 'begin',
            component:resolve=>require(['../views/begin'],resolve),//选择钱包页
        },
        {
            path: '/sicbo',
            name: 'sicbo',
            // component:Sicbo,//游戏页
            component:resolve=>require(['../views/sicbo'],resolve),//游戏页
        },
    ],
})

export default router;



